.wordcounter-page{
    height: 587px;
    background-color: rgb(37, 51, 129);
}
.wordcounter-page h1{
    color: white;
}
.word-counter{
    border: 2px solid green;
    border-radius: 15px;
    width: 800px;
    margin-left: 300px;
    background-color: blueviolet;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.word-counter h3{
    color: rgb(110, 14, 11);
}
.summary{
    border: 2px solid rgb(63, 18, 18);
    border-radius: 15px;
    background-color: rgb(63, 18, 18);
    width: 600px;
    text-align: center;
    margin-top: 20px;
    color: white;
}
.preview{
    color: white;
    margin-left: 200px;
    border-radius: 15px;
    background-color: rgb(22, 88, 66);
    margin-top: 10px;
    border: 2px solid green;
    height: 150px;
    width: 1000px;
}
.preview h3{
    color: rgb(255, 0, 34);
}
.button button{
    color: white;
    border: 2px solid rgb(19, 51, 19);
    padding: 3px 5px;
    border-radius: 5px;
    margin: 5px 5px;
    background-color: rgb(30, 100, 9);
}
.button button:hover{
    background-color:  rgb(75, 221, 31);
}