.navbar{
    /* border: 2px solid green; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 20px;
    background-color: rgb(2, 2, 29);
}
.left-nav ul{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    font-size: 15px;
    text-decoration: none;
    font-weight: bold;
    list-style-type: none;
    margin-top: 15px;
}
.left-nav ul a{
    border: 2px solid rgb(2, 2, 65);
    border-radius: 15px;
    background-color: rgb(3, 46, 59);
    text-decoration: none;
    padding: 5px 7px;
    color: rgb(236, 11, 11);
    /* font-family: 'Times New Roman', Times, serif; */
}
.left-nav ul a:hover{
   background-color: rgb(0, 0, 8); 
}
.right-nav h2{
    color: rgb(216, 51, 10);
    font-family: 'Times New Roman', Times, serif;
    font-weight: bold;
}

