.expence-page{
    /* border: 2px solid goldenrod; */
    margin-top: 5px;
    width: 700px;
    margin-left: 350px;
}
.expences{
    display: flex;
    align-items: center;
    gap: 20px;
    
}
.items{
    padding: 1px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px solid blueviolet;
    border-radius: 10px;
    background-color:  rgb(21, 21, 168);;
    width: 500px;
}
.date{
    padding: 3px 1px;
    text-align: center;
    width: 180px;
    border: 2px solid blueviolet;
    border-radius: 10px;
    background-color: blue;
}
.date span{
    margin: 1px 3px;
    color: white;
}
.Expences-item h3{
    color: rgb(245, 8, 8);
}
.expence-price{
    color: white;
    border: 2px solid blue;
    border-radius: 10px;
    background-color: brown;
    padding: 5px 15px;
}