.Contact{
    padding-top: 1px;
    height: 576px;
    background-color: rgb(37, 51, 129);
}
.contact-me{
    border: 2px solid green;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
    margin: 10px 20px;
    background-color: blue;
    color: white;
}
.contact-me h1{
    font-weight: bold;
}
.form{
    border: 2px solid green;
    border-radius: 10px;
    width: 800px;
    height: 450px;
    margin-left: 300px;
    background-color: rgb(3, 3, 36);
}
.form span{
    margin-left: 300px;
    color: white;
    padding: 2px 5px;
    background-color: brown;
}
.text-enter{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    gap: 30px;
}
.input input{
    width: 400px;
}
.contactbtn{
    margin-top: 30px;
}
.contactbtn button{
    border: 2px solid rgb(6, 1, 22);
    padding: 5px 10px;
    border-radius: 15px;
    background-color: brown;
    color: white;
}
.contactbtn button:hover{
    background-color: rgb(231, 112, 112);
}
.contact-me span{
    color: burlywood;
}
