.Project{
    padding-top: 1px;
    height: 575px;
    background-color: rgb(37, 51, 129);
}
.Project-page{
    display: flex;
    justify-content: space-around;
    
    /* border: 2px solid green; */
    margin: 10px 5px ;
    height: 450px;
}
.heading h1{
    border: 2px solid green;
    border-radius: 20px;
    text-align: center;
    margin: 10px 20px;
    background-color: blue;
    color: white;
    height: 80px;
}
.Project-left{
    border: 2px solid rgb(8, 1, 44);
    border-radius: 10px;
    background-color: rgb(47, 47, 83);
    padding: 5px 10px;
}
.Project-left h2{
    text-align: center;
    color: rgb(207, 20, 20);
}
.Project-left li{
    color: white;
    margin: 10px 1px;
}
.about h3{
    color: beige;
}
.about a{
    border: 2px solid green;
    background-color: rgb(9, 9, 90);
    border-radius: 10px;
    text-decoration: none;
    padding: 10px 10px;
    color: rgb(209, 24, 24);
}
.about a:hover{
    background-color: rgb(24, 24, 58);
    text-decoration: underline;
}
.link{
    text-align: center;
    margin-top: 50px;
}