.new-expence{
    border: 2px solid red;
    width: 700px;
    padding: 10px 5px;
    margin-left: 350px;background-color: aqua;
    border-radius: 15px;

}
.newexpence-controls{
    display: flex;
    justify-content: center;
    gap: 20px;
}
.newexpense-action{
    text-align: center;
    margin-top: 20px;
}
.newexpense-action button{
    background-color: mediumvioletred;
    border: 2px solid red;
    border-radius: 10px;
    padding: 3px 5px;
}
.newexpence-control input{
    border-radius: 10px;
    width: 200px;
}