.details span{
    font-weight: bold;
    color: blue;
    font-size: medium;
}
.Home-container{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 200px;
}
.Home-left img{
    padding-top: 70px;
    width: 500px;
    height: 550px;
}
.Home-right{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.p1{
    font-size: 25px;
}
span{
    /* font-weight: bold; */
    color: rgb(31, 9, 133);
}
h2{
    color: brown;
}
.p2{
    color: rgb(39, 9, 9);
    font-size: 20px;
}
.btn{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    background-color:  rgb(2, 5, 41);
    color: rgb(233, 9, 9);
    border: 2px solid rgb(3, 3, 133);
    border-radius: 15px;
    padding: 5px 15px;
    font-weight: bold;
}
.btn:hover{
    background-color: rgb(124, 124, 194);
    color: rgb(233, 9, 9);
}

