.About{
    padding-top: 5px;
}
.about-page{
    height: 575px;
    background-color: rgb(37, 51, 129);
}
.About-containt{
    /* margin-top: px; */
    display: flex;
    gap: 50px;
}
.About-me{
    border: 2px solid green;
    border-radius: 20px;
    color: white;
    background-color:  blue;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px 20px;
}
.About-me h1{
    font-weight: bold;
}
.About-me span{
    font-size: 20px;
    color: burlywood;
}
hr{
    color: brown;
}
.hilight{
    padding-left: 10px;
    height: 400px;
    border: 2px solid green;
    border-radius: 10px;
    background-color: rgb(11, 11, 34);
    margin-left: 50px;

}
.hilight ul{
    margin-top: 10px;
    /* color: brown; */
}
.hilight ul li{
    margin: 5px 1px;
    color: white;

}
.hilight span{
    font-size: 25px;
    color: rgb(206, 13, 13);
}
.hilight button{
    margin-left: 90px;
    margin-top: 50px;

}
.Resume{
    display: flex;
    background-color: rgb(47, 47, 83);
    padding: 5px 10px;
    border-radius: 10px;
    margin: 2px 30px;
}
.Resume Span{
    color: rgb(231, 40, 40);
    font-size: 20px;
}
.Resume-list{
    margin-top: 10px;
    color: white;
}
.Resume-list li{
    margin: 10px 1px;
}
